@-webkit-keyframes fadeCard {
    from {opacity: 0%;}
    to {opacity: 100%;}
}

@keyframes fadeCard {
    from {opacity: 0%;}
    to {opacity: 100%;}
}

.title {
    margin-top: 0;
}

.card {
    opacity: 100%;
    -webkit-animation-name: fadeCard;
            animation-name: fadeCard;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}


